<template>
  <div data-aos="fade" class="col-12 pb-4 mb-4 white">
    <div
      :class="[
        checkIndex(index) ? 'justify-content-start' : 'justify-content-end',
      ]"
      class="row d-flex m-0 p-0"
    >
      <div class="col-12 col-md-12 col-lg-7 p-0 row">
        <div class="col-12">
          <div
            class="plyr__video-embed"
            id="player"
            v-html="item.primary.video_embed.html"
          ></div>

          <!-- <prismic-embed
            tag="div"
            class="video-container"
            :field="item.primary.video_embed"
          /> -->
        </div>
        <div class="col-12">
          <figcaption class="blockquote-footer mb-0 mt-2">
            {{ item.primary.credit }}
          </figcaption>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Plyr from "plyr";
import "plyr/dist/plyr.css";

export default {
  name: "VideoTestimonial",
  props: {
    item: Object,
    index: Number,
  },
  methods: {
    checkIndex(e) {
      if (e % 2 === 0) {
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    const player = new Plyr("#player", {
    
      controls: ["play-large", "play", "progress", "current-time", 'fullscreen'],
      fullscreen: {
        enabled: true,
        fallback: true,
        iosNative: false,
        container: null,
      },
    });
    window.player = player;
  },
};
</script>

<style>
#player {
  border-radius: 8px;
}
</style>